*{box-sizing:border-box;margin:0;padding:0}body{font-family:Roboto;font-size:1rem;line-height:1.6;background-color:#23292f;color:#fff}a{text-decoration:none;color:#fff}img{width:100%}ul{list-style:none}button{border:none;padding:10px 20px !important}.btn{display:inline-block;background:#ccc;color:#333;border-radius:8px;padding:1rem;cursor:pointer;font-size:1rem;outline:none;transition:all 0.2s ease-in}.btn:hover{background:#333;color:#fff}input[type='text'],input[type='email'],input[type='password'],input[type='date'],input[type='number'],select,textarea{display:block;width:100%;padding:0.4rem;font-size:1.2rem;border:1px solid #ccc;border-radius:8px;box-shadow:0px 6px 8px -8px rgba(0,0,0,0.25);margin:0.1rem 0}.form-container{max-width:500px;margin:1rem auto;overflow:hidden;padding:0 2rem;display:flex;flex-direction:column}.container{max-width:1200px;margin:auto;overflow:hidden;padding:0 2rem}.text-bold{font-weight:900}.text-light{font-weight:lighter !important}.text-primary{color:#443874 !important}.text-success{color:#1b5e20 !important}.text-grey{color:#ccc !important}.text-lg{font-size:1.2rem}.text-mid{font-size:1rem}.text-small{font-size:0.8rem}.text-center{text-align:center}.text-right{text-align:right}.navbar{display:flex;justify-content:space-between;align-items:center;padding:0.7rem 2rem;z-index:1;width:100%;background-color:#161617;border-bottom:solid 1px #161617;border-bottom-right-radius:50px;box-shadow:7px 7px 8px -10px rgba(0,0,0,0.75);opacity:0.9;margin-bottom:1rem}.navbar ul{display:flex}.navbar a{color:#fff;padding:0.45rem;margin:0 0.25rem}.navbar a:hover{color:#ccc}.navbar .welcome span{margin-right:0.6rem}.navbar h1 span{color:#ff0500 !important}.movie-wrapper{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:1rem}.movie-card{background-color:#161617;padding:2rem;margin:1rem 0;border-bottom-right-radius:50px;border-top-left-radius:50px;box-shadow:7px 7px 8px -10px rgba(0,0,0,0.75);cursor:pointer}.movie-card h2{font-size:1.2rem}.movie-card h3{font-size:0.8rem;font-weight:lighter;text-transform:uppercase;letter-spacing:4px}.movie-card p{float:right;font-size:0.8rem}.movie-details-wrapper{background-color:#161617;padding:2rem;border-bottom-right-radius:50px;border-top-left-radius:50px;box-shadow:7px 7px 8px -10px rgba(0,0,0,0.75);display:grid;grid-template-columns:0.4fr 1fr;grid-gap:0.5rem;position:relative}.movie-details-wrapper img{width:180px;border-bottom-right-radius:50px;border-top-left-radius:50px;border-bottom:5px #ff0500 double;border-top:5px #ff0500 double}.movie-details p{text-transform:capitalize}.movie-desc{margin-top:1rem;font-size:0.8rem;font-weight:lighter;text-transform:none !important;opacity:0.7}.movie-details-inner{display:flex;justify-content:space-between}.movie-details-inner span{margin-right:0.6rem}.star{position:relative;top:5px}.next-feature{position:absolute;bottom:2em;text-transform:uppercase !important;font-size:0.8rem;font-weight:900}.hour{display:inline-block;background:#ccc;color:#333;border-radius:8px;padding:0.3rem;cursor:pointer;font-size:0.7rem;outline:none;transition:all 0.2s ease-in;margin-left:1rem}.filter{display:flex;justify-content:center;margin:1rem auto;align-items:center;position:relative}input[type='text']{padding:0.6rem 1rem;font-size:1.2rem;border:1px solid #ccc;box-shadow:0px 6px 10px -8px rgba(0,0,0,0.25);outline:none;background-color:#23292f}.filter i{position:absolute;right:20px}.about{display:flex;justify-content:center;text-align:left;margin:5rem auto;background-color:#161617;padding:2rem;border-bottom-right-radius:50px;border-top-left-radius:50px;box-shadow:7px 7px 8px -10px rgba(0,0,0,0.75)}.about-wrapper{display:flex}.about-wrapper img{flex:2;width:100px;border-bottom-right-radius:50px;border-top-left-radius:50px}.about-wrapper p{flex:6;margin:0 1rem}.about-wrapper a{font-weight:900}.by{text-transform:uppercase;font-size:0.8rem}.social-icons i{margin:0.5rem 1rem}@media (max-width: 1050px){.movie-wrapper{grid-template-columns:1fr}}
